<template>
    <div>
        <p>{{ msg }}</p>
        <p>for - 1</p>
        <p v-for="item in list" :key="item.idx">
            item : {{ item }}
            <br />
            title : {{ item.title }}
            <br />
            text : {{ item.text }}
        </p>
        <p>for - 2</p>
        <p v-for="(value, index) in list" :key="index">{{ value }}</p>
        <p>for - 3 :  </p>
        <If1 v-for="(item, index) in list" :key="index" :item="item"></If1>
        <p>if else</p>
        <input v-model="number" type="text" @keyup="checkNumber" />
        <button @click="resetInput"> </button>
        <p v-if="number == 1">1 .</p>
        <p v-else-if="number == 2">2 .</p>
        <p v-else>1 2  else .</p>
        <p>show</p>
        <p v-show="number == 1">11111111111111111</p>
        <p v-show="visible">{{ number }}</p>
    </div>
</template>

<script>
import If1 from '@/components/study/if1';

export default {
    components: {
        If1,
    },
    data() {
        return {
            msg: ' ',
            number: '',
            visible: false,
            list: [
                { title: '1', text: '1', idx: 1 },
                { title: '2', text: '2', idx: 2 },
                { title: '3', text: '3', idx: 3 },
                { title: '4', text: '4', idx: 4 },
                { title: '5', text: '5', idx: 5 },
                { title: '6', text: '6', idx: 6 },
            ],
        };
    },
    methods: {
        resetInput() {
            this.number = '';
        },
        checkNumber() {
            if (parseInt(this.number, 10) === 2) {
                this.visible = true;
            } else {
                this.visible = false;
            }
        },
    },
};
</script>

<style>
* {
    font-size: 15px;
}
p {
    margin-bottom: 15px;
}
input {
    width: 200px;
    height: 50px;
    line-height: 50px;
    border: 1px solid #000;
}
button {
    width: 100px;
    background: #eee;
    height: 50px;
}
</style>
