<template>
    <div class="container accountRegWrap">
        <div class="inner">
            <div class="locator">
                <ul>
                    <li><a href="#"></a></li>
                    <li><a href="#"></a></li>
                    <li><a href="#"></a></li>
                </ul>
            </div>
            <div class="titleWrap">
                <h3> </h3>
            </div>
            <div class="tableWrap">
                <div class="tableInfo">
                    <h4 class="tableTitle">
                         
                    </h4>
                </div>
                <div class="tableBorder">
                    <table>
                        <colgroup>
                            <col width="12.8%" />
                            <col width="37.2%" />
                            <col width="12.8%" />
                            <col width="37.2%" />
                        </colgroup>
                        <tbody>
                            <tr>
                                <th class="txtL">
                                    
                                </th>
                                <td class="tdInput">
                                    <div class="floatWrap">
                                        <div class="inputBorderBox">
                                            <input
                                                ref="id"
                                                v-model="id"
                                                type="text"
                                                placeholder="5~15  ,  "
                                            />
                                        </div>
                                        <button
                                            class="btnMid color01-bg"
                                            @click="checkId"
                                        >
                                            
                                        </button>
                                    </div>
                                </td>
                                <th class="txtL">
                                    
                                </th>
                                <td class="tdInput">
                                    <div class="inputBorderBox">
                                        <input
                                            ref="name"
                                            v-model="name"
                                            type="text"
                                            placeholder=" ."
                                        />
                                    </div>
                                </td>
                            </tr>
                            <tr>
                                <th class="txtL">
                                    
                                </th>
                                <td class="tdInput">
                                    <div class="inputBorderBox">
                                        <input
                                            ref="tel"
                                            v-model="tel"
                                            type="text"
                                            placeholder="5~15  ,  "
                                            maxlength="13"
                                            @input="replaceTel"
                                        />
                                    </div>
                                </td>
                                <th class="txtL">
                                    
                                </th>
                                <td class="tdInput">
                                    <div class="selectBorderBox">
                                        <select
                                            id=""
                                            ref="gender"
                                            v-model="gender"
                                            name=""
                                        >
                                            <option value="-" selected
                                                >.</option
                                            >
                                            <option
                                                v-for="item in genderOptions"
                                                :key="item.index"
                                                :value="item.itemCd"
                                            >
                                                {{ item.itemNm }}
                                            </option>
                                        </select>
                                    </div>
                                </td>
                            </tr>
                            <tr>
                                <th class="txtL">
                                    
                                </th>
                                <td class="tdInput">
                                    <div class="inputBorderBox">
                                        <input
                                            ref="age"
                                            v-model="age"
                                            type="text"
                                            placeholder="5~15  ,  "
                                        />
                                    </div>
                                </td>
                                <th class="txtL">
                                    
                                </th>
                                <td class="tdInput">
                                    <div class="selectBorderBox">
                                        <select
                                            id=""
                                            ref="area"
                                            v-model="area"
                                            name=""
                                        >
                                            <option value="-" selected
                                                >.</option
                                            >
                                            <option
                                                v-for="item in areaOptions"
                                                :key="item.index"
                                                :value="item.itemCd"
                                            >
                                                {{ item.itemNm }}
                                            </option>
                                        </select>
                                    </div>
                                </td>
                            </tr>
                        </tbody>
                    </table>
                </div>
            </div>
            <div class="btnCenterWrap">
                <router-link
                    to="/account/list"
                    tag="button"
                    class="btnMasterBig color01-bg"
                >
                    
                </router-link>
                <button class="btnMasterBig color02-bg" @click="save">
                    
                </button>
            </div>
        </div>
    </div>
</template>

<script>
// import Vuex from 'vuex';
import axios from '@/axios';
import { mapGetters, mapActions } from 'vuex';

export default {
    data() {
        return {
            gender: '-',
            area: '-',
            id: '',
            tel: '',
            age: '',
            name: '',
            param: {},
            isCheckedId: false,
        };
    },
    computed: {
        ...mapGetters({ genderOptions: 'getGender', areaOptions: 'getArea' }),
    },
    created() {},
    mounted() {
        this.axiosArea();
        this.axiosGender();
    },
    methods: {
        ...mapActions(['axiosArea', 'axiosGender']),
        save() {
            if (this.$validator.isEmpty(this.id)) {
                this.$swal({
                    html: ' .',
                    onAfterClose: res => {
                        console.log(res);
                        this.$refs.id.focus();
                    },
                });
                return;
            }
            if (!this.$validator.isTripathEmailFormat(this.id)) {
                this.$swal({
                    html: '  .',
                    onAfterClose: res => {
                        console.log(res);
                        this.$refs.id.focus();
                    },
                });
                return;
            }

            if (this.$validator.isEmpty(this.name)) {
                this.$swal({
                    html: ' ',
                    onAfterClose: res => {
                        console.log(res);
                        this.$refs.name.focus();
                    },
                });
                return;
            }

            if (this.$validator.isEmpty(this.tel)) {
                this.$swal({
                    html: ' ',
                    onAfterClose: res => {
                        console.log(res);
                        this.$refs.tel.focus();
                    },
                });
                return;
            }

            if (!this.$validator.isTelNoFormat(this.tel)) {
                this.$swal({
                    html: '  .',
                    onAfterClose: res => {
                        console.log(res);
                        this.$refs.tel.focus();
                    },
                });
                return;
            }
            if (this.gender === '-') {
                this.$swal({
                    html: ' .',
                    onAfterClose: res => {
                        console.log(res);
                        this.$refs.gender.focus();
                    },
                });
                return;
            }
            if (!this.$validator.isNumber(this.age)) {
                this.$swal({
                    html: '  .',
                    onAfterClose: res => {
                        console.log(res);
                        this.$refs.age.focus();
                    },
                });
                return;
            }

            if (this.area === '-') {
                this.$swal({
                    html: ' .',
                    onAfterClose: () => {
                        this.$refs.area.focus();
                    },
                });
                return;
            }

            if (!this.isCheckedId) {
                this.$swal({
                    html: '  ',
                });
                return;
            }

            this.param = {
                userId: this.id,
                userNm: this.name,
                telNo: this.tel,
                genderCd: this.gender,
                age: this.age,
                locationCd: this.area,
            };

            console.log('param', this.param);

            axios.post('/user/insert', this.param).then(res => {
                console.log(res);
                this.$swal({
                    html: '.',
                    onAfterClose: () => {
                        this.$router.push({ name: 'accList' });
                    },
                });
            });
        },
        checkId() {
            if (this.$validator.isEmpty(this.id)) {
                this.$swal({
                    html: ' .',
                    onAfterClose: res => {
                        console.log(res);
                        this.$refs.id.focus();
                    },
                });
                return;
            }
            if (!this.$validator.isTripathEmailFormat(this.id)) {
                this.$swal({
                    html: '  .',
                    onAfterClose: res => {
                        console.log(res);
                        this.$refs.id.focus();
                    },
                });
                return;
            }

            axios
                .post('/user/idCheck', {
                    userId: this.id,
                })
                .then(res => {
                    console.log('res', res.data.item); // 0 :   1 : 
                    if (res.data.item === 0) {
                        this.$swal({
                            html: ' .',
                        });
                        this.isCheckedId = true;
                    } else {
                        this.$swal({
                            html: ' .',
                            onAfterClose: () => {
                                this.$refs.id.focus();
                            },
                        });
                        this.isCheckedId = false;
                    }
                });
        },
        replaceTel() {
            this.tel = this.tel
                .replace(/[^0-9]/g, '')
                .replace(
                    /(^02|^0505|^1[0-9]{3}|^0[0-9]{2})([0-9]+)?([0-9]{4})$/,
                    '$1-$2-$3'
                )
                .replace('--', '-');
        },
    },
};
</script>
<style></style>
